*{
    font-family: 'Courier New', Courier, monospace;
}
body{
    padding: 60px;
}
input{
    font-size: 1.3rem;
    width: 100%;
    color: gray;
}
.card-grid{
    display: flex;
    flex-direction: none;
    flex-wrap: wrap;
}
.card{
    align-content: center;
    border: 1px solid lightblue;
    border-radius: 6px;
    margin-bottom: 10px;
    margin-right: 10px;
    overflow: hidden;
}

.card p{
    padding: 5px;
    text-align: center;
}

.card img{
    max-height: 170px;
}